@import url(https://fonts.googleapis.com/css2?family=Ruda:wght@400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fugaz+One&display=swap);
.param-container-normal {
  width: 100%;
  padding: 0 2em;
}

.param-container-expanded {
  width: 100%;
  padding: 0 2em;
  grid-column-end: span 2;
}

.param-header {
  justify-content: space-between;
  align-items: center;
}

.mode-expert h6 {
  color: #512072;
}

.param-name {
  font-size: 15px;
  font-weight: 900;
  margin-bottom: 2px;
  font-size: 15px;
  line-height: 19px;
}

.small-param-desc {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 115.9%;
  letter-spacing: -0.015em;
  margin-bottom: 2px;
}

.param-info-container-hidden {
  display: none;
  opacity: 0;
}

.param-info-container-visible {
  /* background-color: var(--grey); */
  padding: 1em;
  width: 100%;
}

.param-info-container-visible h6 {
  font-size: 0.9em;
  font-weight: bold;
  margin-bottom: 10px;
}

.param-info-container-visible p {
  font-size: 0.7em;
  margin-bottom: 20px;
  width: 90%;
}

#SimulatorNavigation {
  width: 100%;
  display: grid;
  height: 100%;
  grid-gap: 5px;
  gap: 5px;
  font-family: var(--mainFont);
  background-color: var(--mainColor);
  color: white;
  padding: 10px 20px;
}

#SimulatorNavigation .options {
  border: none;
  padding: 0;
}

#SimulatorNavigation .scope {
  font-family: var(--mainFont);
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  border-bottom: 1px solid white;
  margin-bottom: 0;
}

#SimulatorNavigation .icons {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
}

#SimulatorNavigation .icons p,
#SimulatorNavigation .options-container p {
  font-size: 14px;
  text-align: center;
}

#SimulatorNavigation svg {
  display: block;
  margin: 0 auto;
}

#SimulatorNavigation .options-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#Checkbox {
  width: 16px;
  height: 16px;
  border: 2px solid #000000;
  border-radius: 1px;
  position: relative;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  background-color: #e5eaec;
}

#Checkbox .active {
  top: 50%;
  left: 50%;
  width: 50%;
  height: 50%;
  border-radius: 1px;
  -webkit-transform: translate(50%, 50%);
          transform: translate(50%, 50%);
  background-color: #00adb9;
}

#OptionsBox {
  background-color: #e5eaec;
  border:1px solid black;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100;
  padding: 18px 16px;
  display: grid;
  grid-column-gap: 16px;
  -webkit-column-gap: 16px;
          column-gap: 16px;
  grid-template-columns: 15% 1fr 1fr 15%;
}

#OptionsBox svg {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

#OptionsBox .close {
  position: absolute;
  right: 0;
  top: 0;
}

#OptionsBox .param-name {
  font-weight: 900;
  color: #000000;
  font-size: 15px;
  line-height: 18px;
}

#OptionsBox .description {
  font-weight: normal;
  font-style: normal;
  font-size: 15px;
  line-height: 18px;
  margin-bottom: 20px;
}

#OptionsBox .sim-option-form label {
  display: block;
}

#OptionsBox .main-title {
  font-size: 16px;
  font-style: normal;
  font-weight: bold;
  text-transform: uppercase;
}

#OptionsBox .control {
  margin-right: 10px;
}

.modal-parent {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 80%);
}

.modal-content {
  position: relative;
  background-color: white;
  border-radius: 6px;
  padding: 50px 80px;
  max-width: 950px;
  max-height: 90vh;
  overflow-y: scroll;
}

.modal-content table {
  margin-top: 0;
}

.modal-content .close-btn {
  align-self: flex-end;
  margin-top: 50px;
}


/* /// INTRO */
#intro-modal h2 {
  margin-bottom: 20px;
}

#intro-modal p, #intro-modal ol {
  margin: 25px 0;
  font-size: 14px;
}

#intro-modal li {
  margin-left: 50px;
}

#intro-modal ol li {
  margin-bottom: 20px;
}

#intro-modal strong, #intro-modal b {
  color: var(--mainColor);
}

#intro-modal a {
  border-bottom: 2px solid var(--secondaryColor);
}

#intro-modal a:hover {
  opacity: 0.6;
}

#intro-modal svg {
  color: var(--secondaryColor);
}


/* FORM */
form .MuiInput-underline::after {
  border-bottom: 2px solid var(--secondaryColor);
}

form .MuiFormLabel-root.Mui-focused, form .MuiCheckbox-colorSecondary.Mui-checked {
  color: var(--secondaryColor);
}

form .MuiCheckbox-colorSecondary.Mui-checked:hover, form .MuiIconButton-colorSecondary:hover {
  background-color: var(--seethrough);
}

form .MuiFormGroup-root .group {
  margin-top: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

form .MuiFormHelperText-root {
  align-self: flex-end;
}
/* GLOBAL */
.modal-sim {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 500px;
}

.modal-sim h2 {
  align-self: flex-start;
  margin-bottom: 40px;
}


/* VALIDATE SCENARIO CHOOSE TYPE */
.modal-validate .primary-btn:not(:first-child) {
  width: 350px;
  margin-top: 30px;
}

.modal-validate p {
  margin: 10px 0;
  font-size: 0.6em;
  font-style: oblique;
  text-align: right;
  width: 350px;
}

.modal-validate .secondary-btn {
  align-self: flex-end;
}


/* VALIDATE SCENARIO WORKSHOP */
.modal-workshop .input-box {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
  gap: 20px;
}

.modal-workshop .input-box .columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
  gap: 30px;
}

.modal-workshop .input-box .cloud {
  margin-top: 20px;
}

.modal-workshop > p {
  margin: 20px 0;
}

.modal-workshop form {
  width: 100%;
}

.modal-workshop .button-box {
  justify-content: flex-end;
  margin-top: 40px;
}

.modal-workshop button:first-of-type {
  margin-right: 20px;
}

/* CONFIRM */
.modal-confirm .button-box {
  align-self: flex-end;
  justify-content: flex-end;
  margin-top: 40px;
}

.modal-confirm button:first-of-type {
  margin-right: 20px;
}
/* /// SECTIONS */
.sim-page {
  height: 92vh;
  width: 100%;
  background-color: #e5e5e5;
  color: var(--mainText);
  padding: 10px;
  min-height: 500px;
  max-width: 1400px;
}

.sim-container-box {
  display: grid;
  grid-template-rows: 40px 120px 1fr;
  grid-gap: 10px;
  gap: 10px;
  width: 55%;
  margin-right: 10px;
}

.recharts-tooltip-wrapper {
  z-index: 2;
}

/* /// LOADER */
#loader {
  width: 100%;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* /// SIM-NAV */

.title {
  background-color: var(--mainColor);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 0 1rem;
}

#sim-title h1,
#results-top-box h1,
#results-emissions h1 {
  font-family: var(--mainFont);
  font-size: 18px;
  font-weight: normal;
  color: white;
  padding-bottom: 0;
  margin-bottom: 0;
  line-height: 20px;
}

#sim-title .icon-btn span {
  font-size: 1rem;
  margin-left: 8px;
}

#sim-nav-box {
  align-items: center;
  height: 120px;
  padding: 10px;
  background-color: white;
  width: 100%;
}

#sim-nav-box h2 {
  /* font-family: "Circular Std"; */
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
}

#sim-nav-box > div {
  width: 100%;
  justify-content: space-between;
  margin-top: 0.3em;
}

#sim-nav-fr {
  width: 72%;
  justify-content: space-between;
}

#sim-nav-world {
  width: 13%;
}

.sim-nav {
  width: 100%;
}

.sim-nav-categories {
  width: 100%;
  border-top: solid rgba(0, 0, 0, 0.2) 1px;
  justify-content: space-between;
  padding-top: 5px;
}

.sim-nav-category {
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  position: relative;
  display: inline-block;
  height: 45px;
}

.sim-nav-category .sim-nav-category-background {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  border-radius: 5px;
  opacity: 0;
  transition: 1s;
}

.sim-nav-category:hover .sim-nav-category-background {
  opacity: 1;
}

.sim-nav-category .sim-nav-category-icon {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
}

.sim-nav-category .sim-nav-category-icon-helper {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.sim-nav-category img {
  width: 90%;
  max-width: 30px;
  display: inline-block;
  vertical-align: middle;
}

#sim-nav-fr .sim-nav-category {
  width: 16%;
}

#sim-nav-world .sim-nav-category {
  width: 95%;
}

#options {
  width: 12%;
  margin-top: 31px;
  border: none;
}

/* /// MAIN-BOX */
.sim-main-box {
  background-color: white;
  scroll-behavior: smooth;
  overflow-y: scroll;
  padding: 10px;
  width: 100%;
  max-height: 100%;
}

/* /// TITRES CATEGORIES */
.sim-categorie {
  padding: 1.5% 1em;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2%;
  height: auto;
  width: 100%;
  text-align: center;
}

.sim-categorie > div {
  width: 55%;
  justify-content: space-between;
  align-items: center;
}

.sim-jauge {
  width: 100%;
  height: 20px;
  margin: 5px 0;
}

.sim-categorie-emissions {
  font-size: 15px;
  line-height: 19px;
}

.sim-categorie-markers {
  /* justify-content: space-evenly; */
  align-items: baseline;
  width: 100%;
}

.sim-categorie-markers > div {
  align-items: center;
  width: 24%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.MuiTooltip-tooltipArrow {
  background-color: blue;
}

.sim-categorie-markers > div > div {
  width: 100%;
}

/* 
.sim-categorie-markers > div > div:last-of-type {
  width: 70%;
} */

.sim-categorie-markers p {
  font-size: 11px;
  text-align: center;
}

.sim-emissions-2030 {
  color: #0b8c85;
}

.sim-emissions-2020 {
  color: #ff6868;
}

.sim-emissions-2020 span {
  font-size: 9px;
}

.sim-emissions-2030 span {
  font-size: 9px;
}

.sim-categorie-name {
  font-weight: bold;
  font-size: 23px;
  line-height: 32px;
  text-align: left;
  width: 45%;
}

/* /// OPTIONS */
#optionsContainer {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  text-align: center;
  z-index: 9999;
}

#optionsContainer .optionsContainerBackground {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background: rgba(0, 0, 0, 0.5);
}

#optionsContainer .optionsContainerClose {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 2rem;
  cursor: pointer;
}

#scrollOptions {
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  color: #163e59;
}

#scrollOptions > div {
  justify-content: center;
}

.sim-options {
  justify-content: space-between;
  align-items: center;
  padding: 1em 1em 0;
}

.sim-option-box {
  width: 90%;
  margin-bottom: 30px;
}

.sim-option-box p {
  margin-bottom: 0.5em;
  font-size: 0.8em;
}

.sim-option-box label {
  font-size: 1em;
  margin-right: 0.5em;
}

.sim-option-form {
  justify-content: flex-start;
}

.sticky {
  margin-right: -0.3em;
  z-index: 2;
  position: sticky;
  position: -webkit-sticky;
  padding-bottom: 20px;
  margin-bottom: 10px;
  width: 100%;
  max-width: 800px;
  margin: auto;
  text-align: left;
  margin-top: 150px;
}

.sticky .sim-categorie {
  background: #eda54e;
}

.sim-param-box {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  margin-bottom: 2%;
}

.sim-param-radio {
  margin-right: 1em;
}

input[type="radio"] {
  margin-right: 1em;
}

.bigger {
  height: 2.2em;
}

/* /// JAUGES */
.jauge-ext {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.tooltip {
  /* font-weight: 900 !important;
  font-size: 15px !important;
  line-height: 18px !important; */
  position: absolute;
  left: 50%;

  text-align: left;
  white-space: nowrap;
  -webkit-transform: translate(-50%, -150%);
          transform: translate(-50%, -150%);
  transition: all 0.15s ease-in-out;
}

.tooltip::before {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  border: 0.5em solid transparent;
  border-top-color: #000;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.marker1,
.marker2 {
  z-index: 3;
}

