/* /// RESULTS */
/* /////////////////////// */

/* SECTIONS */
.sim-results-box {
  height: 100%;
  width: calc(45% - 10px);
  display: grid;
  grid-template-rows: 40px calc((100% - 250px) / 2) calc((100% - 250px) / 2) 130px 40px;
  grid-gap: 10px;
  gap: 10px;
}

#results-top-box {
  width: 100%;
  height: 100%;
}

#results-climat-box {
  width: 100%;
  height: 100%;
  max-height: 100%;
  background-color: white;
  padding: 10px;
}

/* #results-energie-box {
  width: 100%;
  height: 100%;
  background-color: white;
  padding: 10px;
}

#results-air-box {
  width: 100%;
  height: 100%;
  background-color: white;
  padding: 10px;
} */

.results-content-box {
  width: 100%;
  height: calc(100% - 26px);
  display: flex;
  justify-content: space-between;
}

/* ZONE III : Action buttons & icons */
.sim-results-box .sim-main-btn {
  height: 100%;
}

/* GRAPH BOX */
.graph-box {
  width: 35%;
  height: 100%;
}
.graph-box > p {
  color: #797979;
  font-size: 13px;
  text-align: right;
  margin-bottom: 2px;
}

.graph-compo {
  width: calc(100%);
  height: calc(100% - 15px);
}

/* INDICATOR BOX */
.indicators-box {
  width: calc(65% - 24px);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.indicators-main-box {
  width: 100%;
}
.indicators-secondary-box {
  width: 100%;
  display: flex;
}

.indicators-secondary-box .indicator {
  align-self: flex-end;
}

.indicator {
  margin-right: 10px;
}
.indicator:last-child {
  margin-right: 0;
}

.results-title {
  color: var(--mainColor);
  font-size: 10px;
}

#results-climat-box .indicators-secondary-box .results-title {
  margin-top: -4px;
}

.results-figure {
  font-family: var(--mainPolice);
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  width: 100%;
  height: 45px;
}

.indicator-tooltip {
  font-size: 14px;
  margin-left: 10px;
  opacity: 0.7;
}

.indicators-main-box .results-figure {
  font-size: 25px;
}

