/* TABS : WORKSHOPS + RESULTS*/
.tab-btn {
  background: none;
  border: none;
  display: inline-block;
  min-width: auto;
}

.tab-btn::after {
  content: "";
  background-color: transparent;
  height: 5px;
  display: block;
  margin-top: -2px;
}

.tab-btn.active::after {
  content: "";
  background-color: var(--light-secondaryColor);
  height: 5px;
  display: block;
  margin-top: -2px;
}

.tab-btn.active-bis::after {
  content: "";
  background-color: var(--light-mainColor);
  height: 5px;
  display: block;
  margin-top: -2px;
}

/* BLINKING ARROW : HOME + RESULTS*/
