/* cas mobile / largeurs trop petits : affichage d'un message */

#mobile-message {
  visibility: visible;
  width: 100vh;
  height: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}

#mobile-message > div {
  border: solid 5px white;
  width: 60vw;
  padding: 15px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

#mobile-message p {
  font-size: 18px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-family: var(--mainFont);
  font-weight: 300;
}

/* GLOBAL PAGE */
#workshops {
  width: 100%;
  height: 100%;
}

#workshops .main_container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-content: center;
  min-height: 500px;
  grid-gap: 70px;
  gap: 70px;
}

#workshops .left-pannel h1 {
  align-self: flex-start;
}

#workshops .left-pannel p {
  margin: 60px 0;
  width: 90%;
}

#workshops .left-pannel button {
  align-self: flex-end;
}

#workshops img {
  width: 100%;
}

/* MODAL CREATE */
#modal-create > p {
  margin: 20px 0;
}

#modal-create .input-box {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
  gap: 30px;
}

#modal-create .button-box {
  justify-content: flex-end;
  margin-top: 40px;
}

#modal-create button:first-of-type {
  margin-right: 20px;
}

/* MODAL CONFIRM */
#modal-confirm h2 {
  align-self: flex-start;
  margin-bottom: 30px;
}

#modal-confirm p {
  margin: 10px 0;
}

#modal-confirm p span {
  color: var(--secondaryColor);
  font-weight: bold;
}

#modal-confirm button {
  align-self: flex-end;
  margin-top: 30px;
}
table {
  margin: 50px 0;
  border-collapse: collapse;
  box-shadow: var(--light-shadow);
  width: 100%;
}

thead tr {
  background: var(--gradient-h);
}

tbody tr:nth-of-type(even) {
  background-color: var(--lightgrey);
}

td,
th {
  padding: 10px 15px;
  font-family: var(--mainFont);
  font-weight: normal;
  text-align: center;
}

td {
  font-size: 14px;
}
/* GLOBAL */
#workshop_infos {
  width: 100%;
  height: 100%;
}

#workshop_infos h2,
#workshop_infos h3 {
  align-self: flex-start;
  margin-bottom: 40px;
}

.workshop-specs svg {
  color: var(--mainColor);
  margin-right: 8px;
}

.workshop-specs a {
  font-family: var(--mainFont);
  text-decoration: underline;
}

.workshop-specs a:hover {
  color: var(--mainColor);
}

.workshop-specs input {
  font-size: inherit;
  color: var(--secondaryColor);
}

.workshop-nav .btn,
.sectors-menu .btn {
  font-size: inherit;
}

/* HEADER */
.workshop-summary {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
  justify-items: center;
  grid-gap: 35px;
  gap: 35px;
  font-size: 14px;
}

/* 1 : NAME */
.workshop-name {
  font-size: 1.2rem;
  line-height: 1.5em;
  color: var(--secondaryColor);
  text-transform: uppercase;
  justify-self: flex-start;
}

/* 2 : NAV */
.workshop-nav {
  border-radius: 6px;
  border: 1px solid var(--light-mainColor);
  box-shadow: var(--light-shadow);
  padding: 5px 10px;
}

/* 3 : SPECS + MENU */
.workshop-header-right {
  justify-self: flex-end;
}

.workshop-header-right .primary-btn {
  margin-left: 30px;
}

/* MODAL MENU */
.workshop-options > div, .workshop-options > button:not(:last-of-type) {
  margin-bottom: 40px;
}

.workshop-options .primary-btn {
  width: 350px;
  margin: 0 auto;
}

.workshop-options .primary-btn svg {
  margin-right: 8px;
}

.modal-delete-ws p {
  margin-bottom: 30px;
}

.modal-delete-ws .button-box {
  justify-content: flex-end;
}

.modal-delete-ws button:not(:last-of-type) {
  margin-right: 20px;
}

/* >>> PAGE SYNTHESE */
/* RESULTATS */
#workshop_infos .indicator_box {
  flex-wrap: wrap;
}

#workshop_infos .indicator_main {
  padding: 15px;
  box-shadow: var(--light-shadow);
  border-radius: 6px;
  flex: 1 1;
  margin: 0 20px;
  border: 2px solid var(--light-mainColor);
  position: relative;
}

#workshop_infos .indicator_main p {
  text-align: center;
}

#workshop_infos .indicator_main p:first-of-type {
  font-weight: bold;
  margin-bottom: 10px;
}

#workshop_infos .indicator_main svg {
  position: absolute;
  top: 10px;
  right: 10px;
}

#workshop_infos .main-box .indicator_main {
  border: 2px solid var(--light-mainColor);
}

#workshop_infos .indicator_box.main-box p {
  font-size: 14px;
}

#workshop_infos .indicator_box.main-box svg {
  color: var(--secondaryColor);
}

#workshop_infos .secondary-box .indicator_main {
  border: 2px solid var(--light-secondaryColor);
}

#workshop_infos .indicator_box.secondary-box p {
  font-size: 12px;
}

#workshop_infos .indicator_box.secondary-box svg {
  color: var(--mainColor);
}

#workshop_infos .result-box table {
  margin: 50px 0;
}

/* >>> PAGE SYNTHESE */
/* PARAMETRES */
#workshop_infos .param-box {
  margin: 0 0 50px;
}

#workshop_infos h4 {
  font-size: 18px;
  font-weight: normal;
  font-family: var(--mainFont);
}

#workshop_infos h4::after {
  margin-top: -8px;
}

#workshop_infos .param-box h4 svg {
  margin-right: 10px;
}

/* >>> PAGE SECTEURS */
.sectors-menu {
  border-radius: 6px;
  border: 1px solid var(--light-mainColor);
  box-shadow: var(--light-shadow);
  padding: 5px 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto 40px;
}

#workshop_infos .sector-page table,
#workshop_infos .users-page table {
  margin: 50px 0;
}

#workshop_infos #parameters_distribution_container {
  flex-wrap: wrap;
}

#workshop_infos .parameter_distribution_box {
  flex: 1 1;
  min-width: 350px;
  height: 350px;
  padding: 20px;
  box-shadow: var(--light-shadow);
  margin: 20px;
}

#workshop_infos .parameter_distribution_box > p {
  font-size: 12px;
  font-style: italic;
  margin-bottom: 20px;
}

/* LOADER */
#sim_loader {
  background-color: white;
  opacity: 0.3;
}

.graph_box {width:100%;height:300px;display: flex; justify-content: space-between;}
.graph_box > * {width: 48%;}

.slider_box {width:100%;display: flex; justify-content: space-between;}
.slider_box > * {width: 48%;}
nav {
  display: grid;
  grid-template-columns: 30% auto 30%;
  padding: 0 20px;
  height: 8vh;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1400px;
  background-color: white;
}

nav h4 {
  font-family: var(--funFont);
  color: var(--mainText);
  font-size: 22px;
}

nav h4 span {
  color: var(--secondaryColor);
}

nav img {
  width: 40px;
  margin-right: 15px;
}

nav > p {
  font-weight: 600;
  font-size: 18px;
  font-style: italic;
  padding: 5px 40px;
  color: var(--mainText);
  background-color: var(--secondaryColor);
}

nav > ul {
  list-style-type: none;
  justify-content: flex-end;
}

nav button, a {
  margin-left: 15px;
}

.main-nav button svg {
  color: var(--mainColor);
  margin-right: 8px;
}

*,
*::before,
*::after,
div,
header,
footer,
section,
article,
main {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul[class],
ol[class] {
  list-style: none;
}

input,
button,
textarea,
select {
  outline: none;
}

button, a {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
  margin: 0;
}

@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
